.App {
  text-align: center;
  background-color: beige;
  padding-bottom: 2%;
  padding-top: 2%;
  border-radius: 2%;
  width: 30%;
  margin-left: auto;
  margin-right: auto;
  margin-top: 4%;
  margin-bottom: auto;
  border-width: 2px;
  border-style: solid;
  border-color: rgb(59, 59, 59);
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}



.connect4Button{
  background-color: #4193ad;
  width: 100%;
  height:100%;
  padding-bottom: 100%;
  border-radius: 50%;
  border:1px inset;
  overflow:hidden;
}

#grid{
  border-radius: 5%;
  background-color: #61dafb;
  width: 70%;
  margin-left: auto;
  margin-right: auto;
  padding: 2%;
}
#board{
  margin-left: auto;
  margin-right: auto; 
}
#title{
  font-size: xx-large;
  padding-bottom: 5%;
}

#message{
  padding-top: 2%;
  padding-bottom: 0%;
}

#restart{
  width: 20%;
  background-color: #cfdcec;
  border-color: beige;
  
}